.Wrap{
  background-image: linear-gradient(to right, #6a11cb 0%, #2575fc 100%);  height: 220vh;
}
.innerBox{
  max-width:1200px;
  margin:0 auto;
}
header{
  border-bottom: 1px solid #40375a;
 
}
header>.innerBox{
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
.logo{
  width:100px;
  height: 70px;
  
}
.logo img {
  width: 100%;

  
}
#nav{
  display:flex;
  margin: 0 auto;
  
}
#nav > li {
  margin:50px;
  
}
#nav > li > a{
  color:#eee;
  
}
#nav>li:nth-child(3){
  margin-top: 2%;
}

