.todoitem{
    border:1px solid #eee;
    display:flex;
    line-height:3;
    padding:0 30px;
    border-radius:10px;
    margin-top:10px;
    cursor:pointer;
}
.remove{
    margin-right:20px;
    color:#eee;
    font-weight:bold;
    opacity:0;
}
.todoitem:hover .remove{
    opacity:1;
}
.checked{
    text-decoration: line-through;
    color:#ccc;
}
.todo{
    text-align: center;
}