.aboutWrap{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;
}
.aboutTitle{
    margin-left: 42%;
    margin-bottom: 5%;
}
.box{
    background:white;
    padding:15px 25px;
    border-radius: 10px;
    display:flex;
    justify-content:space-evenly;
    width:32%;
    margin:10px 0;
    box-sizing: border-box;
    margin-left:35%;
}
.box .imgBox{
    width:85px;
    padding:0 15px 0 0;
    text-align: center;
}
.box .imgBox img {
    width: 100%;
}
.box .textBox{
    flex:1;
}
.box .textBox strong{
    color:#000;
}
.box .textBox p{
    color:#666;
}
