
.slick-arrow{
    position:absolute;
    top:50%;
}
.slick-prev{
    left:-20px;
}
.slick-next{
    right:0;
    
}
.pimgBox{
    width: 100%;
    position: relative;
}
.pimgBox img{
    width: 80%;
    margin: 0 auto;
    height: 650px;
    margin-bottom: 2%;
    border-radius: 20px;
    border: 1px solid black;
}


